<template>
  <div class="Login">
    <div>
      <h2>卓越医疗·登录</h2>
    </div>
    <div class="elradio">
      <el-radio v-model="ruleForm.type" label="doctor">医生</el-radio>
      <el-radio v-model="ruleForm.type" label="admin">管理员</el-radio>
    </div>
    <br />账号
    <el-input class="elipt" placeholder="doctorid" v-model="ruleForm.doctorid" clearable></el-input>
    <br />密码
    <el-input class="elipt" placeholder="请输入password" v-model="ruleForm.password" show-password></el-input>
    <br />

    <el-button class="elbt" type="success" @click="submitForm(ruleForm)">提交</el-button>
  </div>
</template>

<script>
// @ is an alias to /src
import { getCheck } from "../network/data";
export default {
  name: "home",
  data() {
    return {
      ruleForm: {
        doctorid: "819109255@qq.com",
        password: "123",
        type: "doctor"
      }
    };
  },
  components: {},
  created() {},
  methods: {
    submitForm(ruleForm) {
        getCheck(ruleForm).then(res => {
          console.log(res);
          
        this.$store.state.session=res.session
        if (res.err_code === 0) {
          this.$router.push({ path: "/index" });
        }
      });
      
    }
  }
};
</script>
<style>
h2 {
  margin-left: 50px;
}
.elipt {
  width: 200px;
}
.elbt {
  margin-left: 100px;
  margin-top: 5px;
}
.Login {
  width: 250px;
  height: 250px;
  margin: 0 auto;
}
.elradio {
  margin-left: 50px;
}
</style>